import {Component, OnInit} from '@angular/core';
import {TranslateService} from "@ngx-translate/core";
import {ModelLanguages} from "./models/model-languages";
import {EnumUrlImg} from "./enums/enum-url-img.enum";
import {EnumLanguage} from "./enums/enum-language.enum";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{

  /**
   * Variable(s) qui pourront être utilisées dans toutes la classe
   */
  public languages  : ModelLanguages[];
  public hideLabel1 : boolean;
  public hideLabel2 : boolean;
  /**
   * Constructeur
   * @param translate
   */
  constructor(public translate: TranslateService) {
  }

  /**
   * Initialisation des variables au moment du chargement de la vue dans le navigateur
   */
  ngOnInit(): void {
    this.hideLabel1 = true;
    this.hideLabel2 = false;
    this.translate.setDefaultLang(EnumLanguage.EN_TRANSLATE); // language par défaut = 'en'
    this.languages = [
      { value     : EnumLanguage.EN,
        viewValue : EnumLanguage.ENGLISH,
        flag      : EnumUrlImg.FLAG_ENGLISH
      },
      { value     : EnumLanguage.FR,
        viewValue : EnumLanguage.FRENCH,
        flag      : EnumUrlImg.FLAG_FRENCH
      }
    ];
  }

  /**
   * Traduction effectuée sur toutes les vues de l'application
   * @param lang
   */
  public changeLanguage(lang: string) {
    // traduction des éléments à traduire dans toutes l'application
    switch (lang) {
      case EnumLanguage.EN:
        this.languages[0].viewValue = EnumLanguage.ENGLISH;
        this.languages[1].viewValue = EnumLanguage.FRENCH;
        this.hideLabel1 = false;
        this.hideLabel2 = true;
        return this.translate.use(EnumLanguage.EN_TRANSLATE);
        break;
      case EnumLanguage.FR:
        this.languages[0].viewValue = EnumLanguage.ANGLAIS;
        this.languages[1].viewValue = EnumLanguage.FRANCAIS;
        this.hideLabel1 = false;
        this.hideLabel2 = true;
        return this.translate.use(EnumLanguage.FR_TRANSLATE);
        break;
    }
  }
}
